export class Customer {
    Name: string;
    CMND: string;
    Email: string;
    Phone: string;


    constructor(name, cmnd, email, phone) {
        this.Name = name;
        this.CMND = cmnd;
        this.Email = email;
        this.Phone = phone;
    }
}



